<template>
  <div>

    <!-- <div class="dropdown">
      <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        Dropdown button
      </button>
      <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" v-for="arr in arrs" :key="arr">
        <a class="dropdown-item" href="#">{{arr.County}}</a>
      </div>
    </div> -->

    <table class="table mt-3">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">城市</th>
          <th scope="col">地區</th>
          <th scope="col">uvi值</th>
          <th scope="col">更新時間</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(arr, i) in arrs" :key="arr">
          <th scope="row">{{i + 1}}</th>
          <td>{{arr.County}}</td>
          <td>{{arr.SiteName}}</td>
          <td>{{arr.UVI}}</td>
          <td>{{arr.PublishTime}}</td>
        </tr>

      </tbody>
    </table>
  </div>
</template>

<script>

import axios from 'axios'
export default {
  name: "mainUvi",
  data() {
    return {
      arrs: []
    }
  },
  mounted() {
    axios.get("http://opendata2.epa.gov.tw/UV/UV.json").then((res) => {
      // console.log(res)
      this.arrs = res.data
    }).catch((err) => {
      console.error(err)
    });
  }
}
</script>



