import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import news from '@/components/news'
import business from '@/components/newsBusiness'
import mainUvi from '@/components/mainUvi'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/news',  //連接到這個路徑後，要到哪一個模板。
      name: 'news',
      component: news
    },
    {
      path: '/uvi',
      name: 'mainuvi',
      component: mainUvi
    },
    {
      path: '/business',
      name: 'mainuvi',
      component: business
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



